.imagesContainer {
    position: relative;
    display: flex;
    flex-direction: row;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
    padding: 1rem;
    overflow-x: auto;
}

.image {
    max-height: 180px;
    max-width: 300px;
    margin-right: 0.6rem;
    cursor: pointer;
}

@media screen and (max-width: 1023px) {
    .imagesContainer {
        max-width: 80vw;
    }
}
